import React, { useRef, useState } from "react";

const ContactForm_Mobile = () => {
  const formRef = useRef(null);
  const [success, setSuccess] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    fetch(
      "https://script.google.com/macros/s/AKfycbyQQX1EsjM41e6EfwK0D4wOR9mU6Gl9A0N3GlA-XYhk7em9eVEfl33hKGHXKyLYxgsFcg/exec",
      {
        method: "POST",
        body: new FormData(formRef.current),
      }
    )
      .then((res) => {
        if (!res.ok) {
          throw new Error("Network response was not ok");
        }
        return res.json();
      })
      .then((data) => {
        setSuccess(true);
        formRef.current.reset();
      })
      .catch((err) => {
        console.error("There was a problem with the fetch operation:", err);
        alert("Submission failed. Please try again.");
      });
  };

  return (
    <form
  ref={formRef}
  onSubmit={handleSubmit}
  className="max-w-md mx-auto p-6 bg-white rounded-lg shadow-2xl border border-gray-300 "
    >
                <h2 className="text-2xl font-bold text-center" style={{ color: "#155D9C", fontFamily: "Trajan, serif" }}>
                    ĐĂNG KÝ TƯ VẤN CHI TIẾT
                </h2>
                                <p className="text-xm mt-3 font-light text-gray-700 text-center pb-6">
                    ĐỂ LẠI THÔNG TIN NHẬN ƯU ĐÃI KHỦNG  <br></br> THAM QUAN DỰ ÁN MIỄN PHÍ
                </p>
                    <div className="mb-4">
                        <label className="block text-gray-700 font-medium mb-2 text-left">
                            <input
                                type="text"
                                name="Name"
                                required
                                placeholder="Họ và tên"
                                className="mt-1 block w-full px-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                            />
                        </label>
                    </div>
                    <div className="mb-4">
                        <label className="block text-gray-700 font-medium mb-2 text-left">
                            <input
                                type="text"
                                name="Message"
                                placeholder="Số điện thoại"
                                required
                                className="mt-1 block w-full px-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                            />
                        </label>
                    </div>
                    <div className="mb-6">
                        <label className="block text-gray-700 font-medium mb-2 text-left">
                            <select
                                name="Type"
                                required
                                className="mt-1 block w-full px-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                            >
                                <option className="text-xs" value="">Phương thức thanh toán</option>
                                <option className="text-xs" value="Trả nhanh chiết khấu lên đến 22%">Trả nhanh chiết khấu lên đến 22%</option>
                                <option className="text-xs" value="Vay thanh toán từ 750tr">Vay thanh toán từ 750tr, miễn lãi và gốc trong 3 năm, chiết khấu 7%</option>
                                <option  className="text-xs"value="Thanh toán theo tiến độ">Thanh toán theo tiến độ, chiết khấu 8%</option>
                            </select>
                        </label>
                    </div>
                    <button
                        type="submit"
                        className="w-full text-white font-semibold py-2 px-4 rounded-md hover:bg-blue-700 transition"
                        style={{ backgroundColor: "#0B3050" }}
                    >
                        Gửi
                    </button>
            {success && (
        <div className="mb-4 text-green-600 font-semibold text-left mt-10"
        style={{ color: "rgb(217, 48, 37)" }}>
          *Cảm ơn bạn đã đăng ký. Chúng tôi sẽ liên hệ với bạn trong thời gian sớm nhất.
        </div>
      )}


      
                      <p className="text-xs mt-3 font-light text-gray-700 text-left pb-6 pl-12 pt-10" style={{ color: "#f09030" }}>
                    - Hỗ trợ tư vấn trực tiếp.<br></br>
                    - Phân tích dự án khách quan.<br></br>
                    - Bảo mật thông tin cá nhân khách hàng.
                </p>

                <p className="text-xs mt-3 font-light text-gray-700 text-center pb-6" style={{ color: "#0B3050" }}>
                    *Bằng việc nhấn "Gửi", bạn đã đồng ý với <br></br>Chính sách Bảo mật Thông tin của chúng tôi.
                </p>

    </form>
  );
};

export default ContactForm_Mobile;