import React, { useState, useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";

import ContactForm_Fixed from '../components/ContactForm_Fixed';


const slides = [
    {
        image: "url(/blanca_img/phoicanhduan1.jpg)",
        title: "BLANCA CITY",
        description: "BLANCA CITY - “Biểu tượng đô thị all-in-one” đầu tiên của Sun Group ngay trung tâm thành phố biển Vũng Tàu.",
        url: "/du-an/blanca-city-vung-tau"

    },
    {
        image: "url(/khangdien_img/phoicanh.jpg)",
        title: "GLADIA Khang Điền",
        description: "Dự án Gladia Khang Điền Thủ Đức là dự án biệt thự cao cấp được chủ đầu tư Khang Điền chuẩn bị quỹ đất từ lâu.",
        url: "/du-an/gladia-khang-dien"

    },
    {
        image: "url(/blanca_img/sycamore.jpg)",
        title: "Sycamore Bình Dương",
        description: "Dự án Sycamore Bình Dương là khu phức hợp cao cấp gồm căn hộ, nhà phố, biệt thự, shophouse...",
        url: "#"

    },
    {
        image: "url(/blanca_img/eco.jpg)",
        title: "ECO RETREAT",
        description: "Sky Retreat – Bản giao hưởng kỳ diệu giữa thiên nhiên hùng vĩ và kiến trúc chạm đến bầu trời. Nơi những giá trị tinh thần được nâng niu...",
        url: "#"

    }
];

function BackgroundSwitcher() {
            useEffect(() => {
            AOS.init();
          }, []);


    const [current, setCurrent] = useState(0);
    const [prev, setPrev] = useState(null);
    const [fade, setFade] = useState(false);

    const prevImage = () => {
        setPrev(current);
        setCurrent((prev) => (prev - 1 + slides.length) % slides.length);
        setFade(true);
    };

    const nextImage = () => {
        setPrev(current);
        setCurrent((prev) => (prev + 1) % slides.length);
        setFade(true);
    };

    // Remove fade after animation
    useEffect(() => {
        if (fade) {
            const timeout = setTimeout(() => setFade(false), 100);
            return () => clearTimeout(timeout);
        }
    }, [fade]);

    // Auto switch every 5 seconds
    useEffect(() => {
        const interval = setInterval(() => {
            nextImage();
        }, 3000);
        return () => clearInterval(interval);
        // eslint-disable-next-line
    }, [current]); // Reset interval on current change


        const [showContactFixed, setShowContactFixed] = useState(false);
    

    return (
        <div>
            {showContactFixed && (
                 <ContactForm_Fixed /> 
            )}
        <section
            style={{
                position: 'relative',
                height: '750px',
                width: '100%',
            }}
            className="flex items-end justify-center text-white block md:hidden"
        >
            {/* Animated background images */}
            <div
                className={`bg-image ${fade ? "fade-out" : ""}`}
                style={{
                    backgroundImage: prev !== null ? slides[prev].image : slides[current].image,
                    backgroundSize: 'cover',
                    backgroundPosition: 'center',
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    width: '100%',
                    height: '750px',
                    zIndex: 1,
                    transition: 'opacity 0.6s',
                    opacity: fade ? 1 : 0,
                }}
            />
            <div
                className={`bg-image ${fade ? "fade-in" : ""}`}
                style={{
                    backgroundImage: slides[current].image,
                    backgroundSize: 'cover',
                    backgroundPosition: 'center',
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    width: '100%',
                    height: '100%',
                    zIndex: 2,
                    transition: 'opacity 0.6s',
                    opacity: fade ? 0 : 1,
                }}
            />

            <div className="container mx-auto lg:flex left-0 right-0"
                style={{
                    position: 'absolute',
                    left: 0,
                    right: 0,
                    bottom: '-18%',
                    margin: '0 auto',
                    zIndex: 10,
                    paddingLeft: "1.5rem"
                }}>


                <div className="relative w-full bg-white shadow-lg overflow-hidden" 
                onClick={() => setShowContactFixed(prev => !prev)}
                     style={{
                        paddingTop: "1.5rem",
                        paddingLeft: "1.5rem",
                        paddingBottom: "1.5rem"
                        }}>
                    <h1 data-aos="fade-right" className="text-4xl font-bold mb-4" style={{ color: '#0b3050' }}>{slides[current].title}</h1>
                    <p data-aos="fade-right" className="text-lg text-gray-700">{slides[current].description}</p>
                    <span className="absolute top-0 right-0 w-12 h-12 bg-gray-200 folded-corner"></span>
                    <a href={slides[current].url} className="inline-block text-white px-4 py-2 
                    cursor-pointer point-that" style={{ backgroundColor: '#0b3050', 
                    marginTop: '20px', marginBottom: '10px'}}>
                        Tìm hiểu ngay
                    </a>
                
                </div>

                <div className="text-center lg:text-left w-full">
                    <button className="hover:bg-blue-700 text-white text-xl"
                        style={{ backgroundColor: "#0B3050",
                            paddingBottom: "0.75rem",
                            paddingTop: "0.75rem",
                            paddingLeft: "2.5rem",
                            paddingRight: "2.5rem"
                         }}
                        onClick={prevImage}
                        aria-label="Previous">
                        &lt;
                    </button>
                    <button className="hover:bg-blue-700 text-white text-xl ml-1"
                        style={{ backgroundColor: "#0B3050",
                            paddingBottom: "0.75rem",
                            paddingTop: "0.75rem",
                            paddingLeft: "2.5rem",
                            paddingRight: "2.5rem",
                            marginLeft: "5px"
                         }}
                        onClick={nextImage}
                        aria-label="Next">
                        &gt;
                    </button>
                </div>

                <style>
                    {`
                    .folded-corner {
                        background: #0B3050;
                        clip-path: polygon(100% 0, 0 0, 100% 100%);
                    }
                    .point-that:hover {
                        background-color: #155D9C !important;
                    }
                    `}
                </style>
            </div>
        </section>
        </div>
    );
}

export default BackgroundSwitcher;